




































































.list-enter-active,
.list-leave-active {
  transition: transform 0.15s, opacity 0.15s;
}
.list-enter,
.list-leave-to {
  opacity: 0;
  transform: translateY(15px);
}
.toast-notification-component {
  z-index: 9999999;
  position: relative;
  .floated-chat-w {
    bottom: 15px;
  }
  .user-avatar {
    .os-icon-robot-2 {
      font-size: 2.4rem;
    }
  }
  .user-w .user-name {
    padding-left: 15px;
  }
  .chat-messages {
    height: 200px;
    overflow-y: auto;
  }

  &.slim-toast-notification-active {
    .floated-chat-w {
      top: 50px;
      left: 50%;
      right: auto;
      bottom: auto;
      width: auto;
      min-width: 80%;
      transform: translate(-50%, -20px);
      line-height: 1.2rem;

      &.active {
        transform: translate(-50%, 0);
      }
    }
  }
}
